@use '../../../../shared/styles/variable/globalIndex';
@use '../../../../shared/styles/variable/global';
@use '../../../../shared/styles/mixins';
@use '../../../../shared/styles/variable/common';

.checkboxGroup {
    display: flex;
    flex-direction: column;
    gap: mixins.rem(10);
}

.toggleSwitch {
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;

    .input {
        display: none; //   
    }

    .slider {
        width: mixins.rem(83); //   (83px)
        height: mixins.rem(42); //   (42px)
        background-color: #ccc; //     
        border-radius: mixins.rem(21); //   ( )
        position: relative;
        transition: background-color 0.3s ease;

        &:before {
            content: '';
            width: mixins.rem(36); //   (36px)
            height: mixins.rem(36);
            background-color: #fff; //  
            border-radius: 50%;
            position: absolute;
            top: mixins.rem(3); //   (3px)
            left: mixins.rem(3); //   (3px)
            transition: transform 0.3s ease;
        }
    }

    .input:checked + .slider {
        background-color: #409798; //      

        &:before {
            transform: translateX(mixins.rem(41)); //    (83px - 36px - 6px)
        }
    }

    .label {
        margin-left: mixins.rem(10);
        font-size: mixins.rem(20);
        font-weight: 700;
        font-family: globalIndex.$font_family_main;
        color: globalIndex.$color_title;
    }
}